import { Link, useLocation } from 'react-router-dom';
import React from "react";
import Intl from 'react-intl-universal';
import {HomeOutlined,OrderedListOutlined,SnippetsOutlined} from '@ant-design/icons';
/*
 * 页面头部
 */
function Headers () {
  let params = useLocation().pathname;//获得地址栏参数
  return (
    <header>
      <div className='topnav'>
        <div className='btn-group'>
          {/* 主页按钮 */}
          <Link to='/home' className={(params === '/home' || params === '/') ? 'btn active' : 'btn'}><HomeOutlined /><span>{Intl.get('HOME')}</span></Link>
          {/* 列表按钮 */}
          <Link className={(params === '/list') ? 'btn active' : 'btn'} to='/list'><OrderedListOutlined /><span>{Intl.get('LIST')}</span></Link>
          {/* 详细页面按钮 */}
          <Link className={(params === '/detail') ? 'btn active' : 'btn'} to='/detail'><SnippetsOutlined /><span>{Intl.get('DETAIL')}</span></Link>
        </div>
      </div>
    </header>
  )
}

export default Headers