import React, { useState } from 'react'
import { Carousel, Row, Col, Modal } from 'antd';
import AdSense from 'react-adsense';
import Intl from 'react-intl-universal';
/*
 * 详细页面内容部分
 */
function DetailContent() {
    // 广告页面状态
    const [Adstate, setAdstate] = useState(true);
    const handleCancel = () => {
        setAdstate(false);
    };
    return (
        <div>
            {Adstate === true ?
                // 弹窗显示google广告
                <div>
                    <Modal title={Intl.get('AD')}
                        open={Adstate}
                        onCancel={handleCancel}
                        width={1000}
                        okButtonProps={{ display: false }}
                        footer={null}
                    >
                        <p>{Intl.get('GOOGLE1')}</p><p>{Intl.get('GOOGLE2')}</p>
                        <p>.</p><p>.</p><p>.</p><p>.</p>
                        {/* google广告显示,目前网站在google审核还没通过 */}
                        {/* 俩种方式 react一种组件 */}
                        <AdSense.Google client="ca-pub-6251011828646312"
                            slot="6754370832"
                            style={{ width: 500, height: 300, float: 'left' }}
                            format='' />
                        {/* 一种google提供代码 */}
                        <amp-ad width="100vw" height="320"
                            type="adsense"
                            data-ad-client="ca-pub-6251011828646312"
                            data-ad-slot="6754370832"
                            data-auto-format="rspv"
                            data-full-width="">
                            <div overflow="">google</div>
                        </amp-ad>
                    </Modal>
                </div>
                : (
                    // 页面轮播图
                    <div id='mainbox'>
                        <Row gutter={[16, 16]}>
                            <Col span={8}>
                                <Carousel>
                                    <div><img src={require("../../img/lb1.png")} alt="" /></div>
                                    <div><img src={require('../../img/lb2.png')} alt="" /></div>
                                    <div><img src={require('../../img/lb3.png')} alt="" /></div>
                                    <div><img src={require('../../img/lb4.png')} alt="" /></div>
                                </Carousel>
                            </Col>
                            {/* 文字介绍 */}
                            <Col span={12}>
                                <p style={{ color: 'white', fontSize: '40px' }}>{Intl.get('WEB3')}</p>
                            </Col>
                        </Row>
                    </div >
                )
            }
        </div>

    )
}

export default DetailContent