import React from 'react'
import DetailContent from '../components/detail/DetailContent'
import Footers from '../components/footer/Footer';
import Headers from '../components/header/Header';
/*
 * 详细内容页面
 */
function Detail() {
  return (
    <div id="content">
      {/* 头部 */}
      <Headers />
      {/* 详细内容部分 */}
      <DetailContent />
      {/* 尾部 */}
      <Footers />
    </div >
  )
}

export default Detail